<template>
  <div class="driverInfo">
    <div class="row" id="driverInfo">
      <div class="col-sm-9">
        <div class="card-deck">
          <div class="card">
            <div class="card-body">

              <h6>Car Information</h6>
              <div class="card-text">
                <div><p class="card-text-title">License Plate Number</p>
                  <p>{{ vehicleData.car.licensePlateNumber }}</p></div>
              </div>
              <div class="card-text">
                <div><p class="card-text-title">Model</p>
                  <p>{{ vehicleData.car.modelName }}</p></div>
              </div>
              <div class="card-text">
                <div><p class="card-text-title">Vehical Status</p>
                  <p><i class="fas fa-circle" v-bind:class="vehicalStatusColorClassObject"></i> {{
                    vehicalDetails.vehicalStatus }}</p></div>
              </div>
            </div>
          </div>
          <div class="card">
            <div class="card-body">
              <h6>Driver Information</h6>
              <div class="card-text">
                <div><img class="rounded-circle profile-img" style="" v-bind:alt="vehicleData.driver.firstname"
                          v-bind:src="vehicleData.driver.driver_image"></div>
              </div>
              <div class="card-text">
                <div><p class="card-text-title">Driver Name</p>
                  <p>{{ vehicleData.driver.firstname }}</p></div>
              </div>
              <div class="card-text">
                <div><p class="card-text-title">User ID</p>
                  <p>{{vehicleData.driver.id }}</p></div>
              </div>
            </div>
          </div>
          <div class="card">
            <div class="card-body">
              <h6>Job Information</h6>
              <div class="card-text">
                <div><p class="card-text-title">Job</p>
                  <p>{{jobDetails.job}}</p></div>
              </div>
              <div class="card-text">
                <div><p class="card-text-title">Job Status</p>
                  <p><i class="fas fa-circle" v-bind:class="jobStatusColorClassObject"></i> {{ jobDetails.jobStatus}}
                  </p></div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-deck">
          <div class="card">
            <div class="card-body">
              <h6>Live Location</h6>
              <div class="card-text">
                <div id="map" v-bind:lat="liveLocationDetails.lat" v-bind:longi="liveLocationDetails.longi"></div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-deck call-logs">
          <div class="card">
            <div class="card-body">
              <h6>Call Logs (04)
                <div class="call-log-btn-head"><i class="fas fa-filter"></i><i class="fas fa-search"></i></div>
              </h6>
              <div class="card-text">
                <table class="table">
                  <thead>
                  <tr>
                    <th>Serial Number</th>
                    <th>Date</th>
                    <th>Time</th>
                    <th>Description</th>
                    <th>Call logs</th>
                    <th>Actions</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for='callLog in callLogDetailsArray'>
                    <td>{{ callLog.callSerialNum }}</td>
                    <td>{{ callLog.callDate }}</td>
                    <td>{{ callLog.callTime }}</td>
                    <td>{{ callLog.description }}</td>
                    <td>{{ callLog.callDuration }}</td>
                    <td class="btn-cell"><i class="fas fa-clipboard-list"></i><i class="fas fa-volume-up"></i></td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-sm-3">
        <div class="card-deck">
          <div class="card license-card">
            <div class="card-body">
              <h6>License Plate Number</h6>
              <h6>{{ vehicleData.car.licensePlateNumber }}</h6>
              <div class="card-text">
                <div><p class="card-text-title">Notes</p>
                  <div class="notes-body">{{ liveCall.notes }}</div>
                </div>
              </div>
              <div class="card-text video-card">
                <div class="video-area d-flex align-items-center justify-content-center"><i class="fas fa-video"></i>
                </div>
                <div class="call-duration">{{ liveCall.duration }}</div>
                <div class="call-btns">
                  <i class="fas fa-microphone-slash"></i>
                  <i class="fas fa-video-slash"></i>
                  <i class="fas fa-phone"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>

</template>

<script>
import CallPopup from '../Utils/CallPopup.js';
import DriverInfoService from '../ApiFile/DriverInfoService'
  export default {
    name: 'DriverInfo',
    created(){

      DriverInfoService.getAccessTokenFromFms()
        .then((response)=>{
          console.log('response',response);
          if(response.data.status===0){
            DriverInfoService.driverInformationApi(response.data.access_token)
              .then((res)=>{
                console.log('driverInfo',res);
                this.vehicleData= res.data.data;
              }).catch((err)=>{
                console.log(err)
            })
          }
        }).catch((error)=>{
          console.log(error)
      });
    },
    data() {
      return {
        show: false,
        hide: true,
        vehicleData:'',
        vehicalDetails: {
          licensePlateNumber: '',
          model: '',
          vehicalStatus: 'Low Battery, Heat',
          vehicalStatusColor: 'red',
        },
        driverDetails: {
          profileImageUrl: 'profile1.PNG',
          name: 'Adam Mitch',
          userId: 'XXXXXXXXXX'
        },
        jobDetails: {
          job: 'XXXXXXXXXX',
          jobStatus: 'Good',
          jobStatusColor: 'green'
        },
        liveLocationDetails: {
          lat: -25.344,
          longi: 131.036
        },
        callLogDetailsArray: [
          {
            callSerialNum: "2359875",
            callDate: "2019/02/20",
            callTime: "12:02:12",
            description: "Left front tire vroken on the bridge",
            callDuration: "01:10:25",
            action1Url: "#",
            action2Url: "#"
          },
          {
            callSerialNum: "2359875",
            callDate: "2019/02/20",
            callTime: "12:02:12",
            description: "Left front tire vroken on the bridge",
            callDuration: "01:10:25",
            action1Url: "#",
            action2Url: "#"
          },
          {
            callSerialNum: "2359875",
            callDate: "2019/02/20",
            callTime: "12:02:12",
            description: "Left front tire vroken on the bridge",
            callDuration: "01:10:25",
            action1Url: "#",
            action2Url: "#"
          }
        ],
        liveCall: {
          notes: "",
          videoUrl: "",
          duration: "00:10:00"
        }
      }
    },
    computed: {

      vehicalStatusColorClassObject: function () {
        var statusColorObj = "";
        if (this.vehicalDetails.vehicalStatusColor == 'red') {
          statusColorObj = {'fa-color-red': true};
        }
        return statusColorObj;
      },
      jobStatusColorClassObject: function () {
        var statusColorObj = "";
        if (this.jobDetails.jobStatusColor == 'green') {
          statusColorObj = {'fa-color-green': true};
        }
        return statusColorObj;
      }
    },
  }
</script>

<style scoped>
  #map {
    height: 400px; /* The height is 400 pixels */
    width: 100%; /* The width is the width of the web page */
  }

  .col-sm-9 {
    padding-right: 0px;
  }

  .col-sm-3 {
    padding-left: 0px;
  }

  .col-sm-3 .card {
    margin-left: 0px;
    background: #eceff3;
  }

  .fa-color-red {
    color: red
  }

  .profile-img {
    width: 100px;
  }

  .fa-color-green {
    color: green
  }

  .call-log-btn-head {
    float: right;
  }

  .call-log-btn-head .fa-search {
    padding-left: 10px
  }

  .call-logs table .btn-cell {
    text-align: center;
    color: #7697f7;
  }

  .call-logs table .btn-cell .fa-clipboard-list {
    padding-right: 10px;
  }

  .license-card .card-text-title {
    border-top: gray 1px solid;
  }

  .license-card .notes-body {
    width: 100%;
    background: white;
    height: 150px
  }

  .video-card .video-area {
    width: 100%;
    background: gray;
    height: 150px;
    text-align: center;
  }

  .call-duration {
    text-align: center;
    font-size: 20px;
    font-weight: bold
  }

  .video-card .call-btns {
    padding: 10px;
    font-size: 16px;
    text-align: center;
    color: #7697f7;
  }

  .video-card .call-btns .fa-microphone-slash {
    padding: 10px;
    border: 1px solid;
    border-radius: 50%;
  }

  .video-card .call-btns .fa-video-slash {
    padding: 10px;
    border: 1px solid;
    border-radius: 50%;
  }

  .video-card .call-btns .fa-phone {
    padding: 10px;
    background: red;
    border-radius: 50%;
    color: white;
  }

</style>



