
import axios from 'axios';
import Config from '../config';

export default {
    getCallLogs(user){
      if(user  == 'admin'){
        return axios({
            method: 'GET',
            url: Config.base_url+'call/calllogs?user='+user,
            contentType : "application/json",
          });
      } else {
        return axios({
            method: 'GET',
            url: Config.base_url+'call/calllogs?user=client:mike',
            contentType : "application/json",
          });
      }
    },
    getOnlineData(){
        return axios({
            method: 'get',
            url: Config.base_url+'taskrouter/listallworkers?workspaceSid=WS872108afd8e6651aca111a31c79a2e4e&activityName=Idle',
            contentType : "application/json",
          });
    },
    getRestAllTask(){
        return axios({
            method: 'get',
            url: Config.base_url+'taskrouter/listalltasks?workspaceSid=WS872108afd8e6651aca111a31c79a2e4e&limit=2',
            contentType : "application/json",
          });
    }
  }



