
import axios from 'axios';
import Config from '../config';
export default {
  getAccessTokenFromFms(){
    return axios({
      method:'post',
      url:'https://btt6y09a8a.execute-api.us-west-2.amazonaws.com/v1/auth/users/login',
      data: JSON.stringify({
        "account_name":"LLTS01",
        "user_name": "LLTS01",
        "hashed_password": "ef797c8118f02dfb649607dd5d3f8c7623048c9c063d532cc95c5ed7a898a64f"
      }),
    })
  },
  driverInformationApi(accessToken){
    return axios({
      method: 'get',
      url: 'https://aim9p7l5w8.execute-api.us-east-1.amazonaws.com/dev/car/details?deviceId=9A3C29F76E13D27A068B4FE33B1DD81C307D81DD',
      headers:{
        Authorization: 'Bearer ' + accessToken
      }
    });
  },


}



