
import axios from 'axios';
var identity = "Umesh";
import Config from '../config';
  export default {
    getAccessTokenApi(){
        return axios({
          method: 'get',
          url: `${Config.base_url}accesstoken?userType=operator&identity=Alice`
        });
      },

    getApiforCapabilityToken(){
      return axios({
        method: 'post',
        url: `${Config.base_url}taskrouter/workercapabilitytoken`,
        data: {
          workspaceSid: 'WSdbca17ee31c7fe1c02e8236c7791ff98',
          workerSid: 'WKc43bdb5a837eb7459e4ac55c3f2b35d7'
        },
        contentType : "application/json",
      });
    },
    getCallLogsOfAdmin(){
      return axios({
        method: 'GET',
        url: 'https://fgv1el6pmh.execute-api.us-east-1.amazonaws.com/dev/call/calllogs?user=admin',
        contentType : "application/json",
      });
    }

  }



