<template>
  <div class="popUp">
    <call-logs></call-logs>
    <div class="modal fade" id="answerCallModal">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <!-- Modal body -->
          <div class="modal-body">
            <img src="@/assets/phoneCalling.png" id="phoneCallImg">
            <div id="licensePlate" style="clear:both">
              <p>License Plate Number</p>
              <p id="licensePlateNum">{{ licensePlateNumber }}</p>
            </div>
            <div id="driver" style="float:left;width:50%;border-right: 1px solid lightgray">
              <p class="">Driver Name</p>
              <p>{{ driverName }}</p>
            </div>
            <div id="carModel" style="float:left;width:50%">
              <p>Model</p>
              <p>{{ carModel }}</p>
            </div>
            <div id="location" style="clear:both">
              <p><i class="fa fa-map-marker"></i>Live Location</p>
              <p>{{ location}}</p>
            </div>
            <button type="button" class="btn btn-primary btn-block" @click="clicked">Answer</button>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import CallLogs from "./CallLogs";
  import DriverInfoService from '../ApiFile/DriverInfoService'


  export default {
    name: 'PopUp',

    data() {return{
      licensePlateNumber:'XXX XXX XXXXXXXXX',
      driverName:'Adam Mitch',
      carModel:'Toyota Prius',
      location: 'ASKEY Computer Corporation 10F, NO.19 JIANKANG RD.,ZHONGHE DIST.'
    }},
    mounted() {
      $('#answerCallModal').modal('show');
    },
    methods:{
      clicked() {
        this.$emit('clicked');
      }
    },
    created(){
      //this.$emit('created');
      DriverInfoService.getAccessTokenFromFms()
        .then((response)=>{
          console.log('response',response);
          if(response.data.status===0){
            DriverInfoService.driverInformationApi(response.data.access_token)
              .then((res)=>{
                console.log('driverInfo',res);
                this.vehicleData= res.data.data;
              }).catch((err)=>{
              console.log(err)
            })
          }
        }).catch((error)=>{
        console.log(error)
      });
    },
    components: {
      CallLogs
    }
  }

  /* $(document).ready(function () {
      $('#answerCallModal').modal('show');
  });*/

</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #location i{
    font-size:24px;
    padding-right:10px;
  }
  #answerCallModal{
    text-align:center;
  }
  .btn-primary {
    background-color: rgb(63,72, 204);
    border-color: rgb(63,72, 204);
  }
  #location i{
    color: rgb(63,72, 204);
  }
  #licensePlate p{
    font-weight: bold;
    color:#4d4d4d;
  }
  #licensePlateNum{
    font-size:1.5rem;
  }
  #driver, #carModel, #location{
    text-align:left;
    padding:1rem;
  }
  #driver p:first-child {
    font-size: 12px;
    font-weight: bold;
    color: gray
  }
  #carModel p:first-child {
    font-size: 12px;
    font-weight: bold;
    color: gray;
  }
  #location p:first-child {
    font-size: 12px;
    font-weight: bold;
    color: gray;
  }
  .modal-body{
    padding: 3rem;
  }

</style>



