<template>
  <div class="driverInfo">
    <button v-on:click="voiceCall">Voice</button>
    <button v-on:click="videoCall">Video</button>
  </div>

</template>

<script>
  import CallPopup from '../Utils/CallPopup.js';
  import ApiIntegration from '../ApiFile/ApiIntegration';
  var mqtt = require('mqtt');
  var client  = mqtt.connect('tcp://iot.eclipse.org:1883');
export default {
  name:'OperatorCalls',
  methods:{
    voiceCall(){
      alert('added voice');
      var JSONObj = '{"callType" : "audio_call", "isCancelCall" : false, "companyName " : "ltts", "roomName" : "askey-test"}';
      client.on('connect', function () {
        console.log('publisher connected');
        setInterval(function() {
// client.publish('askey_ecall', 'ecall mqtt testing');
          client.publish('askey_ecall', JSONObj);
          console.log('mqtt Message Sent');
        }, 10000);
      });
    },
    videoCall(){
      alert('added video')
      var JSONObj = '{"callType" : "video_call", "isCancelCall" : false, "companyName " : "ltts", "roomName" : "askey-test"}';
      client.on('connect', function () {
        console.log('publisher connected');
        ApiIntegration.getAccessTokenApi()
          .then(response => {
            console.log(response);
            console.log('inside apitoen', this);
            const AccessTokenKey = response.data.body.accessToken;
            connect(AccessTokenKey, { name:'my-new-room' }).then(room => {
              console.log(`Successfully joined a Room: ${room}`);
              room.on('participantConnected', participant => {
                console.log(`A remote Participant connected: ${participant}`);
              });
            }, error => {
              console.error(`Unable to connect to Room: ${error.message}`);
            });

          }).catch(err=>{
           console.log('err',err);
        });

        setInterval(function() {
// client.publish('askey_ecall', 'ecall mqtt testing');
          client.publish('askey_ecall', JSONObj);
          console.log('mqtt Message Sent');
        }, 10000);
      });
    }
  },
}

</script>
<style scoped>
  .driverInfo{
    padding-left: 20px;
  }
</style>



